.main {
  padding: 45px 0;
  &__cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 28px;
    margin-top: 40px;
  }
  &__card {
    overflow: hidden;
    img {
      width: 100%;
      height: auto;
      border-radius: 15px;
      transition: all 0.5s;
    }
    &:hover {
      img {
        transform: scale(1.15);
      }
    }
  }
}
.users {
  padding: 40px 0;
  &-cards {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    gap: 40px 28px;
    margin-top: 40px;
  }
  &-card {
    background: #1F1F31;
    border-radius: 15px;
    padding: 25px;
    &__top {
      display: flex;
    }
    &__bottom {
      margin-top: 28px;
    }
    &__img {
      max-width: 190px;
      img {
        width: 100%;
        height: auto;
      }
    }
    &__info {
      flex-shrink: 0;
      margin-left: 20px;
    }
    &__rating {
      display: flex;
      align-items: center;
      background: #161628;
      border-radius: 30px;
      padding: 5px 8px;
      &-desc {
        font-family: 'Neue Einstellung';
        font-weight: 700;
        font-size: 16px;
        line-height: 105%;
        color: #FFFFFF;
        margin-left: 3px;
        display: flex;
        align-items: center;
        span {
          font-family: 'Rubik';
          font-size: 14px;
          color: #9696AE;
          margin-left: 3px;
          display: block;
        }
      }
    }
    &__amount {
      display: flex;
      align-items: center;
      margin-top: 15px;
      &-icon {
        width: 18px;
        height: 18px;
        display: flex;
        justify-content: center;
        align-items: center;
        background: linear-gradient(223.88deg, #9039FF 15.25%, #5297FF 86.11%);
        border-radius: 50%;
        font-weight: 600;
        font-size: 14px;
        line-height: 105%;
        color: #FFC122;
        position: relative;
        margin-top: -3px;
      }
      &-desc {
        font-family: 'Neue Einstellung';
        font-size: 24px;
        line-height: 105%;
        font-weight: 700;
        margin-left: 3px;
        display: flex;
        align-items: center;
        span {
          font-family: 'Rubik';
          font-size: 14px;
          color: #9696AE;
          margin-left: 3px;
        }
      }
      &-hidden {
        position: absolute;
        left: 50%;
        bottom: 100%;
        transform: translate(-50%, 0);
        padding-bottom: 17px;
        opacity: 0;
        pointer-events: none;
        &--active {
          opacity: 1;
          pointer-events: all;
        }
      }
    }
    &__hidden-inner {
      width: 255px;
      background: #434358;
      box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.3);
      border-radius: 15px;
      padding: 10px 12px;
      font-size: 14px;
      line-height: 140%;
      font-weight: 400;
      color: #FFFFFF;
      position: relative;
      span {
        width: 18px;
        height: 18px;
        display: inline-flex;
        justify-content: center;
        align-items: center;
        background: linear-gradient(223.88deg, #9039FF 15.25%, #5297FF 86.11%);
        border-radius: 50%;
        font-weight: 600;
        font-size: 14px;
        line-height: 105%;
        color: #FFC122;
      }
      &:after {
        content: '';
        position: absolute;
        left: 50%;
        bottom: -23px;
        transform: translate(-50%, 0);
        border: 10px solid transparent;
        border-top: 20px solid #434358;
        pointer-events: none;
      }
    }
    &__name {
      font-family: 'Neue Einstellung';
      font-size: 20px;
      line-height: 120%;
      font-weight: 700;
    }
    &__desc {
      font-size: 14px;
      line-height: 120%;
      margin-top: 10px;
    }
  }

}

@media screen and (max-width: 1400px) {
  .users {
    &-cards {
      grid-template-columns: 1fr 1fr 1fr;
    }
  }
}

@media screen and (max-width: 992px) {
  .main {
    &__cards {
      grid-template-columns: 1fr 1fr;
      max-width: 700px;
      margin: 40px auto 0;
    }
  }
  .users {
    &-cards {
      grid-template-columns: 1fr 1fr;
    }
  }
}

@media screen and (max-width: 768px) {
  .main {
    &__cards {
      gap: 15px;
    }
  }
  .users {
    &-cards {
      gap: 15px;
    }
    &-card {
      &__top {
        display: block;
      }
      &__img {
        max-width: 100%;
      }
      &__info {
        margin: 15px 0 0 0;
      }
      &__amount-desc  {
        font-size: 18px;
      }
      &__amount-hidden {
        left: -10px;
        transform: translate(0, 0);
      }
      &__hidden-inner {
        &:after {
          left: 10px;
          transform: translate(0, 0);
        }
      }
      &__name {
        font-size: 18px;
      }
      &__hidden-inner {
        width: 200px;
      }
    }
  }
}

@media screen and (max-width: 576px) {
  .users {
    &-card {
      padding: 15px;
      &__amount-desc {
        font-size: 16px;
      }
      &__name {
        font-size: 16px;
      }
    }
  }
}

@media screen and (max-width: 400px) {
  .main {
    &__cards {
      grid-template-columns: 1fr;
    }
  }
  .users {
    &-cards {
      grid-template-columns: 1fr;
    }
  }
}
