.header {
  background: #242440;
  box-shadow: 0px 4px 15px #000000;
  padding: 18px 0;
  .container {
    max-width: 1820px;
  }
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__left {
    display: flex;
    align-items: center;
  }
  &__right {
    display: flex;
    align-items: center;
  }
  &__logo {
    display: block;
    width: 110px;
    img {
      width: 100%;
      height: auto;
    }
  }
  &__play {
    font-family: 'Neue Einstellung';
    font-weight: 700;
    font-size: 18px;
    line-height: 120%;
    color: #FFFFFF;
    margin-left: 47px;
  }
  &__item {
    position: relative;
    margin-left: 25px;
   &--active {
     .header__item-hidden {
       opacity: 1;
       pointer-events: all;
     }
   }
    &-show {
      position: relative;
      width: 25px;
      height: 5px;
      cursor: pointer;
      span {
        position: absolute;
        width: 5px;
        height: 5px;
        background: #FFFFFF;
        border-radius: 50%;
      }
      span:nth-child(1) {
        position: absolute;
        top: 50%;
        left: 0;
        transform: translate(0, -50%);
      }
      span:nth-child(2) {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
      }
      span:nth-child(3) {
        position: absolute;
        top: 50%;
        right: 0;
        transform: translate(0, -50%);
      }
    }
    &-hidden {
      position: absolute;
      top: 100%;
      left: 0;
      width: 260px;
      opacity: 0;
      pointer-events: none;
      padding-top: 12px;
    }
  }
  &__hidden {
    &-inner {
      background: #242440;
      box-shadow: 0px 4px 25px rgba(0, 0, 0, 0.25);
      padding: 20px;
    }
    &-group {
      position: relative;
      &:not(:first-child) {
        margin-top: 45px;
        &:after {
          content: '';
          position: absolute;
          top: -23px;
          left: 0;
          width: 100%;
          height: 1px;
          background: #44446A;
        }
      }
    }
    &-subtitle {
      font-size: 12px;
      line-height: 120%;
      color: #9595AD;
    }
    &-link {
      display: block;
      font-size: 14px;
      line-height: 120%;
      color: #FFFFFF;
      margin-top: 15px;
    }
  }
  &__btn {
    width: 110px;
    height: 36px;
  }
  &__entrance {
    width: 90px;
    height: 36px;
    margin-left: 15px;
  }
  &__registration {
    width: 165px;
    height: 36px;
    margin-left: 15px;
  }
  &__burger {
    position: relative;
    z-index: 6;
    width: 24px;
    height: 16px;
    cursor: pointer;
    display: none;
    span {
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #ffffff;
      transition: all 0.5s;
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2) {
        top: 50%;
        transform: translate(0, -50%);
      }
      &:nth-child(3) {
        bottom: 0;
      }
    }
    &--active {
      span {
        &:nth-child(1) {
          top: 7px;
          transform: rotate(-45deg);
        }
        &:nth-child(2) {
          display: none;
        }
        &:nth-child(3) {
          bottom: 7px;
          transform: rotate(45deg);
        }
      }
    }
  }
}

@media screen and (max-width: 768px) {
  .header {
    &__burger {
      display: block;
    }
    &__right2 {
      position: absolute;
      z-index: 5;
      top: 0;
      left: -105%;
      width: 100%;
      height: 100vh;
      background: #242440;
      padding: 100px 30px 30px;
      flex-direction: column;
      transition: all 0.5s;
      &--active {
        left: 0;
      }
    }
    &__item-hidden {
      left: 50%;
      transform: translate(-50%, 0);
    }
    &__entrance {
      margin: 25px 0 0 0;
    }
    &__registration {
      margin: 25px 0 0 0;
    }
  }
}

@media screen and (max-width: 576px) {
  .header {
    &__logo {
      width: 90px;
    }
    &__play {
      font-size: 16px;
      margin-left: 30px;
    }
    &__item-hidden {
      left: unset;
      right: 0;
      transform: translate(0, 0);
      width: 200px;
    }
  }
}